<template>
  <v-container>
    <v-row>
      <v-col>
        <h1>MANAJEMEN MENU</h1>
        <p>Pengelolaan Menu Web MJC</p>
      </v-col>
    </v-row>

    <v-divider></v-divider>
    <v-row class="mt-2">
      <v-col class="d-flex justify-start" cols="3">
        <v-text-field
          v-model="cari"
          label="Pencarian"
          dense
          outlined
        ></v-text-field>
      </v-col>
      <v-col class="d-flex">
        <v-btn color="primary" @click="showDialog()"
          ><v-icon>mdi-plus</v-icon>Tambah Menu</v-btn
        >
        <v-btn class="ml-2" color="success" rounded
          ><v-icon>mdi-refresh</v-icon>Refresh</v-btn
        >
      </v-col>
    </v-row>

    <v-row class="mt-0">
      <v-col>
        <v-card elevation="4" class="pa-4">
          <v-data-table
            :headers="gridHeaders"
            :items="arrData"
            class="elevetion-1"
            :search="cari"
            :items-per-page="20"
            dense
          >
            <template v-slot:[`item.icon`]="{ item }">
              <v-icon>{{ item.icon }}</v-icon>
              {{ item.icon }}
            </template>
            <template v-slot:[`item.opsi`]="{ item }">
              <v-btn small text @click="edit(item.id_menu)"
                ><v-icon>mdi-pencil</v-icon></v-btn
              >
              <v-btn small color="error" text @click="deleteMenu(item.id_menu)"
                ><v-icon>mdi-close-thick</v-icon></v-btn
              >
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </v-row>

    <v-dialog
      v-model="dialogTambah"
      max-width="720px"
      transition="dialog-transition"
    >
      <v-card>
        <v-card-title> Tambah Menu </v-card-title>
        <v-card-text>
          <v-text-field
            v-model="form.group_menu"
            label="Group Menu"
            outlined
            dense
          ></v-text-field>
          <v-text-field
            v-model="form.kode_menu"
            label="Kode Menu"
            outlined
            dense
          ></v-text-field>
          <v-text-field
            v-model="form.text"
            label="Nama Menu"
            outlined
            dense
          ></v-text-field>
          <v-text-field
            v-model="form.route"
            label="Route"
            outlined
            dense
          ></v-text-field>
          <v-text-field
            v-model="form.icon"
            label="Icon"
            outlined
            dense
          ></v-text-field>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn v-if="staEdit == true" color="primary" @click="updateMenu()">
            Update
          </v-btn>
          <v-btn v-else color="success" @click="simpanMenu()"> Simpan </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <notifications position="bottom right" />
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      gridHeaders: [
        { text: "ID MENU", value: "id_menu" },
        { text: "GROUP MENU", value: "group_menu" },
        { text: "KODE MENU", value: "kode_menu" },
        { text: "NAMA MENU", value: "text" },
        { text: "ROUTE", value: "route" },
        { text: "IKON", value: "icon" },
        { text: "AKTIF", value: "flag_aktif" },
        { text: "TANGGAL", value: "created_at" },
        { text: "OPSI", value: "opsi" },
      ],

      arrData: [],
      arrDetail: [],

      cari: "",
      dialogTambah: false,
      staEdit: false,

      form: {
        id_menu: "",
        group_menu: "",
        kode_menu: "",
        text: "",
        route: "",
        icon: "",
        aktif: "",
      },
    };
  },

  mounted() {
    this.loadData();
  },
  methods: {
    deleteMenu(idMenu) {
      axios
        .post("menu/delete/", {
          id_menu: idMenu,
        })
        .then((res) => {
          if (res.data.success == true) {
            this.loadData();
            alert(res.data.message);
          } else {
            alert(res.data.message);
            console.log(res.data.data);
          }
        });
    },
    updateMenu() {
      console.log(this.form);
      axios.post("menu/update", this.form).then((res) => {
        if (res.data.success == true) {
          this.dialogTambah = false;
          this.loadData();
          this.staEdit = false;
          this.$notify({
            type: "success",
            text: "Update menu berhasil, " + this.form.text,
          });
          // alert(res.data.message);
          console.log(res.data);
        } else {
          alert(res.data.message);
          console.log(res.data.data);
        }
      });
    },
    edit(idMenu) {
      this.staEdit = true;
      axios
        .get("menu/" + idMenu)
        .then((res) => {
          this.form = res.data.data[0];
          this.dialogTambah = true;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    simpanMenu() {
      axios
        .post("menu/create", this.form)
        .then((res) => {
          if (res.data.success == true) {
            this.dialogTambah = false;
            this.loadData();
            this.$notify({
              type: "success",
              text: "Tambah menu berhasil, " + this.form.text,
            });
            console.log("berhasil");
          } else {
            console.log("gagal");
            alert(res.data.message);
            console.log(res.data.data);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    showDialog() {
      this.resetForm();
      this.dialogTambah = true;
      this.staEdit = false;
    },
    resetForm() {
      this.form.group_menu = "";
      this.form.kode_menu = "";
      this.form.text = "";
      this.form.route = "";
      this.form.icon = "";
    },

    loadData() {
      axios
        .get("menu")
        .then((res) => {
          this.arrData = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

