<template>
  <v-container>
    <v-row>
      <v-col class="d-flex justify-start">
        <div>
          <v-btn color="primary" text to="/penjualan/pengiriman" class="mr-3"
            ><v-icon x-large>mdi-arrow-left-bold</v-icon></v-btn
          >
        </div>
        <div class="text-h5 mt-1">INPUT PENGIRIMAN</div>
      </v-col>
    </v-row>
    <v-divider></v-divider>
    <v-row class="mt-2">
      <v-col cols="auto" lg="6">
        <v-card outlined>
          <v-card-text>
            <v-text-field
              label="No. Pengiriman (Otomatis)"
              dense
              outlined
              append-icon="mdi-truck"
              readonly
              background-color="blue-grey lighten-5"
            ></v-text-field>
            <v-menu
              v-model="data.pickerTanggal"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  label="Tanggal"
                  :value="formatDate(data.tanggal)"
                  append-icon="mdi-calendar"
                  readonly
                  class="mr-2"
                  v-bind="attrs"
                  v-on="on"
                  dense
                  outlined
                  background-color="light-blue lighten-5"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="data.tanggal"
                @input="data.pickerTanggal = false"
              ></v-date-picker>
            </v-menu>
            <div class="d-flex justify-space-around">
              <v-text-field
                label="No. Transaksi"
                dense
                outlined
                class="mr-2"
                readonly
                :value="data.noTransaksi"
              ></v-text-field>
              <v-btn color="light-blue lighten-4" @click="showTransaksi()"
                ><v-icon>mdi-magnify</v-icon></v-btn
              >
            </div>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="auto" lg="6">
        <v-card outlined>
          <v-card-text>
            <v-text-field
              label="Customer"
              :value="data.namaCustomer"
              dense
              outlined
              append-icon="mdi-account"
              readonly
              background-color="blue-grey lighten-5"
            ></v-text-field>
            <v-text-field
              label="Tanggal Transaksi"
              :value="formatDate(data.tanggalTrans)"
              dense
              outlined
              append-icon="mdi-calendar-blank-outline"
              readonly
              background-color="blue-grey lighten-5"
            ></v-text-field>
            <!-- <v-text-field
              label="Alamat"
              dense
              outlined
              append-icon="mdi-calendar-blank-outline"
              readonly
              background-color="blue-grey lighten-5"
            ></v-text-field> -->
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row class="mt-0">
      <v-col>
        <v-card outlined>
          <v-card-text>
            <v-card outlined>
              <v-data-table
                :headers="data.headers"
                :items="data.items"
                :loading="data.loading"
                :search="data.search"
                dense
              >
              </v-data-table>
            </v-card>
            <div class="d-flex justify-end mt-4">
              <v-btn color="success" @click="create()">SIMPAN</v-btn>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-dialog
      v-model="transaksi.dialog"
      max-width="1000px"
      transition="dialog-transition"
    >
      <v-card class="pb-4">
        <v-card-title> LIST TRANSAKSI PENJUALAN </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-2">
          <v-card outlined>
            <v-data-table
              :headers="transaksi.headers"
              :items="transaksi.items"
              :loading="transaksi.loading"
              :search="transaksi.search"
              dense
            >
              <template v-slot:[`item.tanggal`]="{ item }">
                {{ formatDate(item.tanggal) }}
              </template>
              <template v-slot:[`item.opsi`]="{ item }">
                <v-btn text @click="pilihTransaksi(item.no_bukti)"
                  ><v-icon>mdi-check-bold</v-icon></v-btn
                >
              </template>
            </v-data-table>
          </v-card>
        </v-card-text>
      </v-card>
    </v-dialog>
    <notifications position="bottom right"></notifications>
  </v-container>
</template>

<script>
import axios from "axios";
import swal from "sweetalert";
export default {
  data() {
    return {
      data: {
        headers: [
          { text: "KODE BARANG", value: "kode_bahan", divider: true },
          { text: "NAMA BARANG", value: "nama_bahan", divider: true },
          { text: "JUMLAH", value: "jumlah" },
        ],
        items: [],
        loading: false,
        search: "",
        pickerTanggal: false,
        tanggal: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
        kodeCustomer: "",
        namaCustomer: "",
        tanggalTrans: "",
        noTransaksi: "",
      },
      transaksi: {
        dialog: false,
        headers: [
          { text: "NO. TRANS", value: "no_bukti" },
          { text: "TANGGAL", value: "tanggal" },
          { text: "NAMA CUST.", value: "nama" },
          { text: "ALAMAT", value: "alamat" },
          { text: "OPSI", value: "opsi" },
        ],
        items: [],
        loading: false,
        search: "",
        noBukti: "",
      },
    };
  },
  methods: {
    showTransaksi() {
      this.transaksi.dialog = true;
      this.loadTransaksi();
    },
    async loadTransaksi() {
      this.data.loading = true;
      await axios.post("penjualan/transaksi/forpengiriman").then((res) => {
        if (res.status != 200) {
          this.$notify({ type: "error", text: res.data.error });
          return;
        }
        this.transaksi.items = res.data.data;
      });
      this.data.loading = false;
    },
    pilihTransaksi(noBukti) {
      this.transaksi.noBukti = noBukti;
      this.transaksi.dialog = false;
      this.loadTransaksiDetail();
    },
    async loadTransaksiDetail() {
      await axios
        .post("penjualan/transaksi/print", {
          noBukti: this.transaksi.noBukti,
        })
        .then((res) => {
          if (res.status != 200) {
            this.$notify({ type: "error", text: res.data.error });
            return;
          }
          this.data.items = res.data.data;
          this.data.noTransaksi = res.data.data[0].no_bukti;
          this.data.kodeCustomer = res.data.data[0].kd_customer;
          this.data.namaCustomer = res.data.data[0].nama;
          this.data.tanggalTrans = res.data.data[0].tanggal;
        });
    },
    async create() {
      await axios.post("penjualan/pengiriman/create", this.data).then((res) => {
        if (res.status != 200) {
          // this.$notify({ type: "error", text: res.data.error });
          swal("Gagal", res.data.message, "error");
          return;
        }
        swal("Sukses", res.data.message, "success");
        this.resetAll();
      });
    },
    resetAll() {
      this.data.kodeCustomer = "";
      this.data.namaCustomer = "";
      this.data.tanggalTrans = "";
      this.data.noTransaksi = "";
      this.data.items = [];
    },
  },
};
</script>

<style>
</style>